.selectors {
  justify-content: center;
  align-items: center;
  display: flex;
}

button {
  text-align: center;
  color: #fff;
  cursor: pointer;
  background: #6e6d70;
  border: none;
  border-radius: 6px;
  margin-right: 10px;
  padding: 6px 14px;
  font-size: 30px;
  transition: box-shadow, all 1s, all ease-in-out;
}

button:hover, button:focus {
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

